'use client';

import React from 'react';
import { motion } from 'framer-motion';
import FluidProjectCard from './FluidProjectCard';
import { PROJECT_SHOWCASE_CARDS } from '../../lib/constants';
import { useDynamicLayout } from '../../hooks/useDynamicLayout';

const FluidProjectGrid: React.FC = () => {
  const layout = useDynamicLayout();

  return (
    <motion.div
      className="relative w-full"
      initial={{ opacity: 0, y: 30 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.8, ease: 'easeOut' }}
    >
      <div
        className="mx-auto"
        style={{
          maxWidth: layout.maxWidth,
          paddingLeft: layout.containerPadding,
          paddingRight: layout.containerPadding,
        }}
      >
        <div
          className="grid"
          style={{
            gridTemplateColumns: `repeat(${layout.columns}, minmax(0, 1fr))`,
            gap: layout.gap,
          }}
        >
          {PROJECT_SHOWCASE_CARDS.map((card, index) => {
            const columnSpan =
              layout.viewport === 'desktop'
                ? card.shape.gridOffset?.columnSpan ?? 1
                : 1;
            const rowSpan =
              layout.viewport === 'desktop'
                ? card.shape.gridOffset?.rowSpan ?? 1
                : 1;

            return (
              <div
                key={card.id}
                style={{
                  gridColumn: columnSpan > 1 ? `span ${columnSpan}` : undefined,
                  gridRow: rowSpan > 1 ? `span ${rowSpan}` : undefined,
                }}
              >
                <FluidProjectCard
                  card={card}
                  index={index}
                  viewport={layout.viewport}
                />
              </div>
            );
          })}
        </div>
      </div>
    </motion.div>
  );
};

export default FluidProjectGrid;
